    descr:
      'В 1977 г. ученые обнаружили необычный узкополосный сигнал из космоса, получивший название «Вау!» Этот сигнал до сих пор остается наиболее подходящим кандидатом на роль сигнала, отправленного иной цивилизацией. В двух своих новых исследованиях астроном-любитель и популяризатор науки Альберто Кабальеро идентифицировал звезду, из системы которой был послан этот сигнал, а также оценил вероятность того, что сигнал был отправлен недружественной по отношению к галактическим соседям цивилизацией.Кабальеро известен в первую очередь своим Youtube каналом The Exoplanets Channel, на котором он рассказывает об исследованиях экзопланет, поисках внеземного интеллекта и межзвездных путешествиях. Он также занимается координацией проекта Habitable Exoplanet Hunting Project (HEHP), представляющего собой международную сеть профессиональных астрономов и астрономов-любителей, занимающихся изучением экзопланет из близлежащих звездных систем. В частности, проект ставит целью обнаружение потенциально обитаемых планет в системах звезд спектрального класса G (желтые карлики), K (оранжевые карлики) или М (красные карлики), расположенных на расстоянии не более 100 световых лет от Земли и не демонстрирующих вспышечной активности.В 2020 г. проект HEHP объявил об обнаружении экзопланеты размером с Сатурн, обращающейся в обитаемой зоне вокруг родительской звезды. В этом же году Кабальеро наблюдал солнцеподобную звезду, из системы которой мог быть отправлен сигнал «Вау!». Кабальеро описал это открытие в новом исследовании, увидевшем свет в журнале International Journal of Astrobiology в начале мая.Открытие этой системы заставило Кабальеро задуматься над вопросом безопасности при возможном взаимодействии с представителями внеземной цивилизации. Поэтому в еще одном исследовании Кабальеро провел статистический анализ, направленный на оценку вероятности существования в нашей Галактике потенциально враждебных внеземных цивилизаций, а также того, что хотя бы одна из этих цивилизаций обнаружит сигналы, идущие с Земли (и осуществит вторжение).Для оценки вероятности вторжения враждебной цивилизации Кабальеро использовал частоту вторжений одних стран на территорию других из истории земной цивилизации за последние 100 лет. Только 51 страна из 195 стран мира осуществляла вторжение на территорию другого государства. Ученый нашел, что по мере «созревания» цивилизации частота вторжений снижается. Если экстраполировать эти результаты на тот период развития человечества, когда оно превратится в цивилизацию I типа по Кардашёву, способную к межзвездным путешествиям, частота возможных вторжений существенно снижается.Кроме того, Кабальеро исследовал возможность того, что мы превратимся в «агрессивную цивилизацию», став цивилизацией I типа по Кардашёву. Его анализ показал, что таких цивилизаций в границах зоны, где могут быть обнаружены наши сигналы, может насчитываться не более четырех. Поэтому вероятность вторжения внеземной цивилизации является отнюдь не самым насущным вопросом – по сравнению с ней, вероятность самоуничтожения цивилизации представляется более реальной, считает автор.',
